import './App.css';
import CssBaseline from '@mui/material/CssBaseline';
import ContentCopyIcon from '@mui/icons-material/ContentCopy';
import { Typography, TextField, Autocomplete, Box, Button, IconButton, Snackbar, CircularProgress } from '@mui/material';

import React from 'react';
import {useState} from 'react';



function App() {
  // console.log(stateList);
  const [isLoading, setIsLoading] = useState(false);
  const [open, setOpen] = useState(false);
  const [reps, setReps] = useState([]);
  const states = [];
  const [userName, setUserName] = useState('[Your Name]')

  for(let usState of stateList){
    states.push({ label: usState });
  }

  function googleApi(zipcode, state){
    setIsLoading(true);
    setReps([]);
    const key = process.env.REACT_APP_APIKEY;
    const query = `${zipcode}, ${state}`;
    const GETURLSAPI = `https://www.googleapis.com/civicinfo/v2/representatives?key=${key}&address=${query}`;
    fetch(GETURLSAPI, {
      method:"GET",
      mode: "cors",
      cache:"no-cache",
      headers:{
        "Content-Type": "application/json"
      }
    })
    .then((response) => response.json())
    .then(function(data){
      let newReps = []
      for (let official of data.officials){
        if (official['urls'][0].includes('.senate.gov') || official['urls'][0].includes('.house.gov')){
          let names = official.name.split(' ');
          // console.log(names);
          let isCosponsor = 'No';
          for (let cosponsor of Cosponsors) {
            if(cosponsor.includes(names[0]) && cosponsor.includes(names[names.length - 1])){
              isCosponsor = 'Yes'; 
              break;
            }
          }
          const repId = newReps.length;
          const rep = (
            <tr key={official.name}>
              <td>{official.name}</td>
              <td>{isCosponsor}</td>
              <td><a target='_blank' rel="noreferrer"  href={official['urls'][0]}>{official['urls'][0]}</a></td>
              <td>
                <div id={`rep${repId}`} className='copy-text'>
                  <TextField
                    className="copy-input"
                    label="Subject/Comments Line"
                    multiline
                    rows={30}
                    defaultValue={message2.replace('[CNAME123]', official.name).replace('[YNAME123]', userName)}
                    variant='filled'
                    sx={{ width: 700, display: 'none' }}
                  />
                  <IconButton onClick={() => copyToClipboard(`rep${repId}`)} className="copyIcon" color='primary'>
                    <ContentCopyIcon  />
                  </IconButton>
                  <Snackbar
                    message="Copied to clipboard"
                    anchorOrigin={{ vertical: "top", horizontal: "center" }}
                    autoHideDuration={2000}
                    onClose={() => setOpen(false)}
                    open={open}
                  />
                </div>
              </td>
            </tr>
          )

          newReps.push(rep);
          // console.log(newReps)
        }
        setReps(newReps);
        setIsLoading(false);
      }
    });
  }



  function loadUrls(){
    let zipcodeInput = document.querySelector('#address');
    zipcodeInput.select();
    console.log(zipcodeInput.value)
    let stateInput = document.querySelector('#state');
    stateInput.select()
    console.log(stateInput.value);

    googleApi(zipcodeInput.value, stateInput.value);


  }

  function changeName(name){
    setUserName(name);
  }

  function copyToClipboard(id) {
    let copyText = document.querySelector(`#${id}`);

    let copyInput = copyText.querySelector(".copy-input");
    let input = copyInput.querySelector("textarea");
    input.select();
    input.setSelectionRange(0, 99999);
    // console.log(input.value)
    navigator.clipboard.writeText(input.value);

    setOpen(true);
  }

  return (
    <React.Fragment>

     <CssBaseline />
      <Typography variant="h2" align="center" sx={{ mt: 6}} className='title'>Project Save TikTok
        <img src="tiktokIcon200.png"></img>
      </Typography>


      <div className='form-container'>
        <TextField id="name" label="Your Name (Optional)" variant="standard" onChange={e => changeName(e.target.value)}/>
      <Box
        component="form"
        noValidate
        className='form'
        sx={{ py: 7 }}
      >
        <TextField id="address" label="Enter Zipcode (or address)" variant="standard" />
        <Autocomplete
          disablePortal
          id="state"
          options={states}
          renderInput={(params) => <TextField {...params} label="State" />}
          sx={{ml: 4, width: 200}}
        />
      </Box>

      <Button variant="outlined" className='submitBtn' sx={{ display: 'block' }} onClick={() => loadUrls()}>Load URLs</Button>

      {isLoading && (<center><CircularProgress sx={{mt: 3}}/></center>)}
      
      {reps.length !== 0 && (
        <table>
          <tbody>
              <tr>
                <th>Name</th>
                <th>Have they Cosponsored
                  banning <br />TikTok yet? (s.686 RESTRICT Act)</th>
                <th>CONTACT THEM!!</th>
                <th>Copy<br/>Message<br/>Button</th>
                
              </tr>
            {reps}
          </tbody>
          
        </table>
      )}

      </div>

      <Box
        className='info'
        sx={{  flexDirection:'row' }}
      >
        
        <Typography variant="h6"  >Possible Issues/Federal Agencies to use:</Typography>
        <ul>
          <li>Arts and Humanities</li>
          <li>Congress and Elections</li>
          <li>Science and Technology</li>
          <li>Homeland Security</li>
        </ul>

        <div id="copy-title" className='copy-text'>
          <TextField
            className="copy-input"
            id="subject"
            label="Title"
            multiline
            rows={1}
            defaultValue="TikTok Community Stands Together"
            variant='filled'
            sx={{ width: 700}}
          />
          <IconButton onClick={() => copyToClipboard('copy-title')} className="copyIcon">
            <ContentCopyIcon />
          </IconButton>
          <Snackbar
            message="Copied to clipboard"
            anchorOrigin={{ vertical: "top", horizontal: "center" }}
            autoHideDuration={2000}
            onClose={() => setOpen(false)}
            open={open}
          />
        </div>
        
        <div id="copy-text" className='copy-text'>
          <TextField
            className="copy-input"
            id="subject"
            label="Subject/Comments Line"
            multiline
            rows={30}
            defaultValue={message}
            variant='filled'
            sx={{ width: 700, }}
          />
          <IconButton onClick={() => copyToClipboard('copy-text')} className="copyIcon">
              <ContentCopyIcon/>
          </IconButton> 
           <Snackbar
            message="Copied to clipboard"
            anchorOrigin={{ vertical: "top", horizontal: "center" }}
            autoHideDuration={2000}
            onClose={() => setOpen(false)}
            open={open}
          />
        </div>
        


      </Box>
  
      
    </React.Fragment>
  );
}

const message = "Dear [Congressperson's Name],\n\nI am writing to express my opposition to the proposed ban on TikTok in the s.686 Restrict Act and to inform you that I will not support your candidacy if you choose to support such a ban.  \n\nAs a member of the TikTok community, I have seen firsthand the positive impact that the app has had on people's lives. TikTok has become a platform for creativity, self-expression, and community building. It has brought people together from all over the world, fostering a sense of global unity and understanding. In particular, the app has been a lifeline for many during the past few years, providing an outlet for people to connect and share their experiences during these difficult times.\n\nThe proposed ban on TikTok would not only be an attack on the rights of millions of TikTok users but would also stifle creativity and free speech. It would be a step backward in the fight for equality and social justice, as TikTok has given voice to marginalized communities and has been a platform for promoting social change.\n\nI urge you to stand with the TikTok community and oppose any efforts to ban the app. As a constituent and a member of the TikTok community, I will not support any candidate who does not support the rights of TikTok users.  Furthermore, as an active volunteer in my community, I will make sure to volunteer and vote against you in your next primary election if you fail to support TikTok and its community.  \n\nThank you for your attention to this important matter.\n\n\nSincerely,\n[Your Name]";

const message2 = "Dear [CNAME123],\n\nI am writing to express my opposition to the proposed ban on TikTok in the s.686 Restrict Act and to inform you that I will not support your candidacy if you choose to support such a ban.  \n\nAs a member of the TikTok community, I have seen firsthand the positive impact that the app has had on people's lives. TikTok has become a platform for creativity, self-expression, and community building. It has brought people together from all over the world, fostering a sense of global unity and understanding. In particular, the app has been a lifeline for many during the past few years, providing an outlet for people to connect and share their experiences during these difficult times.\n\nThe proposed ban on TikTok would not only be an attack on the rights of millions of TikTok users but would also stifle creativity and free speech. It would be a step backward in the fight for equality and social justice, as TikTok has given voice to marginalized communities and has been a platform for promoting social change.\n\nI urge you to stand with the TikTok community and oppose any efforts to ban the app. As a constituent and a member of the TikTok community, I will not support any candidate who does not support the rights of TikTok users.  Furthermore, as an active volunteer in my community, I will make sure to volunteer and vote against you in your next primary election if you fail to support TikTok and its community.  \n\nThank you for your attention to this important matter.\n\n\nSincerely,\n[YNAME123]"

